import React from 'react'
import Typed from 'react-typed';

const Banner = () => {
    return (
        <div className='bg-[#f0f0f0] w-full py-[50px] mt-10 rounded-[100px] font-[cursive] sm:bg-[#d3e9e9a1]' id='banner' >
            <div className="max-w-[1240px] mx-auto text-center font-light text-[#2d0020]">
                <p className='text-[60px] sm:text-[35px]'>
                    We<br/>
                    <span className='font-bold text-[red] text-[70px] sm:text-[40px]'>
                        <Typed strings={["'Manufacture'", "'Delivers'", "'Lay Pavers'"]} typeSpeed={200}
                            backSpeed={100} loop={true} className='border-b-[10px] border-[blue]'/>
                    </span>
                </p>
            </div>
        </div>
    )
}

export  default Banner;
